.wrapper {
  background: var(--petrol-100);
}

.container {
  @media screen and (max-width: 480px) {
    padding: 20px;
  }
}
.cardWrapper {
  width: 100%;
  color: #fff;
  display: flex;
  justify-content: center;
  @media screen and (max-width: 480px) {
    padding: 0 !important;
    border: none;
  }
  .cardBody {
    padding: 24px 18px;
    width: 382px;
    border: 1px solid var(--petrol-80);
    border-radius: 24px;
    @media screen and (max-width: 860px) {
      width: 482px;
    }
    @media screen and (max-width: 480px) {
      padding: 0;
      border: none;
    }
  }
}
